.zl-staff-item-icon span {
    display: block;
    position: static;
    -webkit-transition: opacity 0.7s, -webkit-transform 0.4s;
    transition: opacity 0.7s, -webkit-transform 0.4s;
    -o-transition: opacity 0.7s, -o-transform 0.4s;
    -moz-transition: opacity 0.7s, transform 0.4s, -moz-transform 0.4s;
    transition: opacity 0.7s, transform 0.4s;
    transition: opacity 0.7s, transform 0.4s, -webkit-transform 0.4s,
        -moz-transform 0.4s, -o-transform 0.4s;
}

.zl-staff-item-icon span:nth-child(2) {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    opacity: 0;
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -o-transform: rotate(90deg);
    transform: rotate(90deg);
}

.zl-checkbox_rounded label>input:checked~.zl-staff-item.zl-checkbox_rounded-box .zl-staff-item-icon span:nth-child(1),
.zl-navbar.-open .zl-navbar-toggle button.zl-staff-item .zl-staff-item-icon span:nth-child(1),
.zl-staff-item.-active .zl-staff-item-icon span:nth-child(1) {
    opacity: 0;
    -webkit-transform: rotate(-90deg);
    -moz-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    -o-transform: rotate(-90deg);
    transform: rotate(-90deg);
}

.zl-checkbox_rounded label>input:checked~.zl-staff-item.zl-checkbox_rounded-box .zl-staff-item-icon span:nth-child(2),
.zl-navbar.-open .zl-navbar-toggle button.zl-staff-item .zl-staff-item-icon span:nth-child(2),
.zl-staff-item.-active .zl-staff-item-icon span:nth-child(2) {
    opacity: 1;
    -webkit-transform: rotate(0);
    -moz-transform: rotate(0);
    -ms-transform: rotate(0);
    -o-transform: rotate(0);
    transform: rotate(0);
}

