.zl-staff-item-text a {
    position: relative;
    display: inline-block;
    margin: 15px 0 0 0;
    text-decoration: none;
    color: blue;
}

.zl-staff-item-text a:before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 1px;
    background: currentColor;
    -webkit-transition: -webkit-transform 0.4s cubic-bezier(0.4, 0.22, 0.21, 1.04);
    transition: -webkit-transform 0.4s cubic-bezier(0.4, 0.22, 0.21, 1.04);
    -o-transition: -o-transform 0.4s cubic-bezier(0.4, 0.22, 0.21, 1.04);
    -moz-transition: transform 0.4s cubic-bezier(0.4, 0.22, 0.21, 1.04),
        -moz-transform 0.4s cubic-bezier(0.4, 0.22, 0.21, 1.04);
    transition: transform 0.4s cubic-bezier(0.4, 0.22, 0.21, 1.04);
    transition: transform 0.4s cubic-bezier(0.4, 0.22, 0.21, 1.04),
        -webkit-transform 0.4s cubic-bezier(0.4, 0.22, 0.21, 1.04),
        -moz-transform 0.4s cubic-bezier(0.4, 0.22, 0.21, 1.04),
        -o-transform 0.4s cubic-bezier(0.4, 0.22, 0.21, 1.04);
}

.zl-checkbox_rounded .zl-staff-item-text label>input:focus~a.zl-checkbox_rounded-box:before,
.zl-staff-item-text .zl-checkbox_rounded label>input:focus~a.zl-checkbox_rounded-box:before,
.zl-staff-item-text a:focus:before,
.zl-staff-item-text a:hover:before {
    -webkit-transform: scaleX(0);
    -moz-transform: scaleX(0);
    -ms-transform: scaleX(0);
    -o-transform: scaleX(0);
    transform: scaleX(0);
}

.zl-checkbox_rounded label>input:checked~.zl-staff-item.zl-checkbox_rounded-box .zl-staff-item-text,
.zl-navbar.-open .zl-navbar-toggle button.zl-staff-item .zl-staff-item-text,
.zl-staff-item.-active .zl-staff-item-text {
    display: block;
    opacity: 1;
    -webkit-animation: zl-staff-item-show 0.6s both;
    -moz-animation: zl-staff-item-show 0.6s both;
    -o-animation: zl-staff-item-show 0.6s both;
    animation: zl-staff-item-show 0.6s both;
}

.zl-staff-head {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -moz-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    cursor: pointer;
    -webkit-transition: opacity 0.7s;
    -o-transition: opacity 0.7s;
